import React, { useEffect } from 'react';
import * as d3 from 'd3';
import './PieChart.scss';

var data1 = [
{
    title: "定罪",
    value: 141
},
{
    title: "簽保守行為",
    value: 108
},
{
    title: "撤銷控罪",
    value: 40
},
{
    title: "被判照顧或保護令",
    value: 3
},
{
    title: "無罪釋放",
    value: 12
}];

const PieChart = () => {
  const piechart = React.createRef();

  useEffect(() => {
    const width = 450;
    const height = 600;
    const margin = 0;


    var radius = Math.min(width, height) / 2 - margin;

    var svg = d3.select(piechart.current)
      .append("svg")
      .attr("viewBox", "0 0 " + width + " " + height)
      .append("g")
      .attr("transform", "translate(" + width / 2 + "," + width / 2 + ")");


    var color = d3.scaleOrdinal()
      .domain(data1)
      .range(["#6DB76D", "#C41F1F", "#A1A1A1", "#FFFFFF", "#FFD600"]);

    var pie = d3.pie()
      .value(function(d) {
        return d.value;
      }).sort(function(a, b) {
        return d3.ascending(a.title, b.title);
      })(data1);

    var arc = d3.arc()
      .outerRadius(radius - 10)
      .innerRadius(radius/2);

    var g = svg.selectAll("arc")
      .data(pie)
      .enter().append("g")
      .attr("class", "arc")
      .on('mouseover', function (d, i) {
          d3.select(this).transition()
              .duration('50')
              .attr('opacity', '.95');
      })
      .on('mouseout', function (d, i) {
          d3.select(this).transition()
              .duration('50')
              .attr('opacity', '1');
      })
      .attr('transform', 'translate(0, 0)');
  
    g.append("path")
      .attr("d", arc)
      .style("fill", function (d) {
          return color(d.data.title);
      });
    // Usually you have a color scale in your chart already
  
    // Add one dot in the legend for each name.
    var size = 20
    svg.selectAll("mydots")
      .data(pie)
      .enter()
      .append("rect")
        .attr("x", - width/3)
        .attr("y", function(d,i){ return 20 + i*(size+5) + width/2}) // 100 is where the first dot appears. 25 is the distance between dots
        .attr("width", size)
        .attr("height", size)
        .style("fill", function (d) {
          return color(d.data.title);
      });
  
    // Add one dot in the legend for each name.
    svg.selectAll("mylabels")
      .data(pie)
      .enter()
      .append("text")
        .attr("x", - width/3 + size*1.2)
        .attr("y", function(d,i){ return 20 + i*(size+5) + (size/2) + width/2}) // 100 is where the first dot appears. 25 is the distance between dots
        .style("fill", "#fff")
        .text(function(d){ return d.data.title + " " + d.data.value + "人"})
        .attr("text-anchor", "left")
        .style("alignment-baseline", "middle")

    function arcTween(d) {
      var interpolate = d3.interpolate(this._current, d);
      this._current = interpolate(0);                     
      return function(t) { 
        return arc(interpolate(t));                       
      };     
    }
  })

  return (
    <div className="piechart section" id="piechart">
      <div ref={piechart}></div>
    </div>
  )
}

export default PieChart;