import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3';
import './BarChartD.scss';

const BarChartD = (props) => {
  const barchartD = useRef(null)
  useEffect(() => {
    
    var barData = [{
      "id": 'test1',
      "verynotagree": 1.4,
      "notagree": 3.8,
      "neutral": 11.5,
      "agree": 44.5,
      "veryagree": 38.7,
    }, {
      "id": 'test2',
      "verynotagree": 8.0,
      "notagree": 17.8,
      "neutral": 25.3,
      "agree": 25.8,
      "veryagree": 23.2,
    }, {
      "id": 'test3',
      "verynotagree": 10.4,
      "notagree": 22.7,
      "neutral": 28.1,
      "agree": 27.7,
      "veryagree": 11.1,
    }, {
      "id": 'test4',
      "verynotagree": 14.1,
      "notagree": 23.5,
      "neutral": 24.9,
      "agree": 25.8,
      "veryagree": 11.7,
    }, {
      "id": 'test5',
      "verynotagree": 16.5,
      "notagree": 23.0,
      "neutral": 26.1,
      "agree": 23.6,
      "veryagree": 10.8,
    }, {
      "id": 'test5',
      "verynotagree": 13.4,
      "notagree": 26.4,
      "neutral": 27.5,
      "agree": 23.3,
      "veryagree": 9.4,
    }];
      var drawChart = function(data) {
        var margin = {
          t: 30,
          r: 20,
          b: 20,
          l: 70
        },
          width = 1000,
          height = 300;
        var w = width ;
        var h = height - margin.t - margin.b;
        
        
        var y = d3.scaleLinear().domain([0, data.length]).range([h - margin.t - margin.b, 0]);
        var x = d3.scaleLinear().range([0, w - margin.r - margin.l]);
        var x_label = d3.scaleLinear().range([0, w + 40]);
      
        var color = ["#C41F1F", "#EC6868", "#FFFFFF", "#6DB76D", "#389C38"];
        var getRanges = function(d) {
          var vn, na, ne, ag, va;
          var total = d.verynotagree + d.notagree + d.neutral + d.agree + d.veryagree
          vn = (d.verynotagree / total);
          na = (d.notagree / total);
          ne = (d.neutral / total);
          ag = (d.agree / total);
          va = (d.veryagree / total);
          return [[0, vn.toFixed(4)], [vn.toFixed(4), na.toFixed(4)], [(vn + na).toFixed(4), ne.toFixed(4)], [(vn + na + ne).toFixed(4), ag.toFixed(4)], [(vn + na + ne + ag).toFixed(4), va.toFixed(4)]];
        };

        const levels = ["非常不認同","不認同","一般","認同","非常認同"];

        var xAxis = d3.axisBottom()
          .scale(x_label)
          .ticks(5)
          .tickFormat(function(d, i){return levels[i]})

        
        const labels = ["堵路","攻擊警察","投擲磚頭","私了","投汽油彈", "縱火", ""];
        var formatDay = function(d) {
          return labels[d % labels.length];      
        }
        
        
        var yAxis = d3.axisLeft()
          .scale(y)
          .tickFormat(formatDay);
      
        var svg = d3.select(barchartD.current).append("svg")
          .attr("viewBox", "0 0 " + w + " " + h)
          .attr("transform", "translate(0," + (margin.t) + ")");
      
        svg.append("g")
          .attr("class", "x axis")
          .attr("transform", "translate(" + (margin.l - 30) + "," + (margin.t - 20) + ")")
          .call(xAxis);
      
        svg.append("g")
          .attr("class", "y axis")
          .attr("transform", "translate(" + (width) + "," + (margin.t) + ")")
          .call(yAxis);

      
        data.forEach(function(td, j) {
          svg.append("g")
            .attr("transform", "translate(" + margin.l + "," + (y(j) + margin.t - 25/2) + ")")
            .selectAll("rect")
            .data(getRanges(td))
            .enter()
            .append("rect")
            .attr("class", "bar")
            .style("fill", function(d, i) {
              return color[i];
            })
            .attr("width", function(d, i) {
              return x(d[1]);
            })
            .attr("height", 28)
            .attr("transform", function(d, i) {
              return "translate(" + (x(d[0]) - margin.l) + ",0)";
            });
        });
      };
      drawChart(barData);
    
  }, [])

  return (
    <div
      className="chart section"
      ref={barchartD}
    >
      <h2 className="chart__title">{props.title}</h2>
    </div>
  )
}

export default BarChartD