import React, { useEffect, useRef, useState } from 'react'
import * as d3 from 'd3';
import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import './BarChartC.scss';

gsap.registerPlugin(ScrollTrigger);

const BarChartC = (props) => {
  const barchartC = useRef(null)
  const summary = React.createRef();
  const [progress, setProgress] = useState(0);
  useEffect(() => {
    var data = [{
      "name": "19-06",
      "arrest": 73,
      "charge": 44,
      "desc": "",
      "date": "19年6月"
    },
    {
      "name": "19-07",
      "arrest": 224,
      "charge": 88,
      "desc": "",
      "date": "19年7月"
    },{
      "name": "19-08",
      "arrest": 751,
      "charge": 171,
      "desc": "",
      "date": "19年8月"
    },{
      "name": "19-09",
      "arrest": 765,
      "charge": 202,
      "desc": "",
      "date": "19年9月"
    },{
      "name": "19-10",
      "arrest": 1189,
      "charge": 296,
      "desc": "",
      "date": "19年10月"
    },{
      "name": "19-11",
      "arrest": 2899,
      "charge": 581,
      "desc": "",
      "date": "19年11月"
    },{
      "name": "19-12",
      "arrest": 687,
      "charge": 100,
      "desc": "",
      "date": "19年12月"
    },{
      "name": "20-01",
      "arrest": 669,
      "charge": 68,
      "desc": "",
      "date": "20年1月"
    },{
      "name": "20-02",
      "arrest": 346,
      "charge": 24,
      "desc": "",
      "date": "20年2月"
    },{
      "name": "20-03",
      "arrest": 377,
      "charge": 8,
      "desc": "",
      "date": "20年3月"
    },{
      "name": "20-04",
      "arrest": 67,
      "charge": 6,
      "desc": "",
      "date": "20年4月"
    },{
      "name": "20-05",
      "arrest": 929,
      "charge": 7,
      "desc": "",
      "date": "20年5月"
    },{
      "name": "20-06",
      "arrest": 230,
      "charge": 5,
      "desc": "",
      "date": "20年6月"
    }];
  
data = data.sort(function (a, b) {
    return d3.descending(a.name, b.name);
})

var margin = {
    top: 50,
    right: 0,
    bottom: 0,
    left: 0
};

var width = 960 - margin.left - margin.right,
    height = 338 - margin.top - margin.bottom;

var svg = d3.select("#barChartC").append("svg")
    .attr('viewBox', '0 0 ' + (width + margin.left + margin.right) + ' ' + (height + margin.top + margin.bottom))
    .append("g")
    .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

var x = d3.scaleLinear()
    .range([0, width])
    .domain([0, d3.max(data, function (d) {
        return d.arrest;
    })]);
    

var y = d3.scaleBand()
    .rangeRound([height, 0])
    .domain(data.map(function (d) {
        return d.name;
    }));


var yAxis = d3.axisLeft()
    .scale(y)
    .tickSize(0)
    .tickFormat(function(d, i){return data[i].desc})



var bars = svg.selectAll(".bar")
    .data(data)
    .enter()
    .append("g")

bars.append("rect")
  .attr("class", "bar__arrest")
  .attr("y", function (d) {
      return y(d.name);
  })
  .attr("height", y.bandwidth() * 0.85)
  .attr("x", 60)
  .attr("width", function (d) {
      return x(d.arrest)/width * (width - 60);
  });

bars.append("text")
  .attr("class", "label")
  .attr("y", function (d) {
      return y(d.name) + y.bandwidth() / 2 + 4;
  })
  .attr("x", function (d) {
    return x(d.arrest)/width * (width - 120) + 70;
  })
  .text(function (d, i) {
      return (i === data.length - 1 ||  i === data.length - 6) ? d.arrest : "";
  })
  .style("fill", function (d, i) {
    return (i === data.length - 1) ? "#C41F1F" : "#fff";
  })
  .style("font-size", "16px");

bars.append("rect")
  .attr("class", "bar__highlight")
  .attr("y", function (d) {
      return y(d.name);
  })
  .attr("height", y.bandwidth() * 0.85)
  .attr("x", 60)
  .attr("width", function (d) {
      return x(d.charge)/width * (width - 60);
  });

bars.append("text")
  .attr("class", "label")
  .attr("y", function (d) {
      return y(d.name) + y.bandwidth() / 2 + 2;
  })
  .attr("x", 0)
  .text(function (d) {
      return d.date;
  })
  .style("fill", '#fff');
    
  var gy = svg.append("g")
    .attr("class", "y axis")
    .attr("transform", "translate( " + (width - 10)  + ", 0 )")
    .call(yAxis)
    
  // create a list of keys
  var keys = ["拘捕", "檢控"]

  // Usually you have a color scale in your chart already
  var color = d3.scaleOrdinal()
    .domain(keys)
    .range(["#fff", "#C41F1F"]);

  // Add one dot in the legend for each name.
  var size = 20
  svg.selectAll("mydots")
    .data(keys)
    .enter()
    .append("rect")
      .attr("x", function(d,i){ return width - 150 + i*(size+55)})
      .attr("y", - margin.top) // 100 is where the first dot appears. 25 is the distance between dots
      .attr("width", size)
      .attr("height", size)
      .style("fill", function(d){ return color(d)})

  // Add one dot in the legend for each name.
  svg.selectAll("mylabels")
    .data(keys)
    .enter()
    .append("text")
      .attr("x", function(d,i){ return width - 150 + size*1.4 + i*(size+55)})
      .attr("y", - margin.top + (size/2)) // 100 is where the first dot appears. 25 is the distance between dots
      .style("fill", function(d){ return color(d)})
      .text(function(d){ return d})
      .attr("text-anchor", "left")
      .style("alignment-baseline", "middle")
  
  ScrollTrigger.create({
    trigger: barchartC.current,
    start: "top top",
    end: "+=200%",
    scrub: true,
    pin: "#barChartC",
    invalidateOnRefresh: true
  });

  const barArray = gsap.utils.toArray(".bar__arrest");
  const barArrayHighlight = gsap.utils.toArray(".bar__highlight");
  const sentences = gsap.utils.toArray(".chart__title span");
  const numbers = gsap.utils.toArray(".number")

  ScrollTrigger.create({
    trigger: barchartC.current,
    start: "top top",
    end: "+=200%",
    scrub: true,
    invalidateOnRefresh: true,
    snap: 1 / 4,
    onUpdate: self => {
      let index = Math.round(self.progress * (sentences.length - 1))
      setProgress(self.progress)
    },
  }); 

  barArray.forEach((bar, i) => {
    gsap.timeline({
      scrollTrigger: {
        trigger: barchartC.current,
        scrub: true,
        start: "top top",
        end: "+=200%",
        invalidateOnRefresh: true
      }
    })
    .fromTo(bar,
      {
        scaleX: 1
      },
      {
        scaleX: 1,
        duration: 1,
        delay: 1
      }
    )
    .fromTo(bar,
      {
        scaleX: 1
      },
      {
        scaleX: 2899 / data[i].arrest,
        duration: 1,
      }
    )
    .fromTo(summary.current,
      {
        opacity: 0
      },
      {
        opacity: 1,
        duration: 1,
      }
    )
  });
  
  barArrayHighlight.forEach((bar_h, i) => {
    let tl = gsap.timeline({
      scrollTrigger: {
        trigger: barchartC.current,
        scrub: true,
        start: "top top",
        end: "+=200%",
        invalidateOnRefresh: true
      },
    })
    tl.fromTo(bar_h,
      {
        opacity: 0
      },
      {
        opacity: 1,
        duration: 1,
        delay: 1
      }
    )
    .fromTo(bar_h,
      {
        scaleX: 1
      },
      {
        scaleX: 2899 / data[i].arrest,
        duration: 1
      },
    )
    
    tl.set({}, {}, "+=1");
  });

  // numbers.forEach((number, i) => {
  //   let tl_label = gsap.timeline({
  //     scrollTrigger: {
  //       trigger: barchartC.current,
  //       scrub: true,
  //       start: "top top",
  //       end: "+=200%",
  //       invalidateOnRefresh: true
  //     },
  //   })
  //   tl_label.fromTo(number,
  //     {
  //       opacity: 1
  //     },
  //     {
  //       opacity: 0,
  //       duration: 1,
  //     }
  //   )
  // });
  
  }, [])

  return (
    <div
      className="chart section"
      ref={barchartC}
    >
      <div id="barChartC">
        <h2 className="chart__title">
          <span style={{ opacity: 3 - progress * 8}}>拘捕潮去年6月開始，在11月爆發高峰，此後並未平息。</span>
          <span style={{ opacity: progress < 0.5 ? progress * 8 - 3 : 5 - progress * 8 }}>拘捕不代表檢控，進入司法程序的案件遠落後於拘捕人數。</span>
          <span style={{ opacity: progress < 0.75 ? progress * 8 - 5 : 7 - progress * 8 }}>去年6月，檢控率達6成，後來逐步跌至約2成，2020年2月以後皆不足一成。</span>
          <span style={{ opacity: progress*8 - 7}}>至今已有逾17%被捕者，即1620人在調查後獲無條件釋放，有輿論認為警方濫浦。</span>
        </h2>
        <svg viewBox="0 0 960 338" className="chart__summary" ref={summary}>
          <g transform="translate(0,50)">
            <rect y="0" height="284" x="60" width="154.7" fill="white"></rect>
            <rect y="0" height="284" x="204" width="600.6" fill="#C41F1F"></rect>
            <rect y="2" height="280" x="804.6" width="154.7" fill="#222" stroke="#C41F1F" stroke-width="4"></rect>
            <text x="824.6" y="100" fill="#C41F1F" style={{fontSize: "40px"}}>17%</text>
            <text x="824.6" y="200" fill="#C41F1F" style={{fontSize: "20px"}}>無條件釋放</text>
          </g>
          <g transform="translate(0,50)">
            <rect y="0" height="284" x="0" width="60" fill="#222"></rect>
          </g>
        </svg>
      </div>
      
      {/* <h2 className="chart__title">
        不過，拘捕不代表檢控，進入司法程序的案件遠落後拘捕人數
      </h2>
      <h2 className="chart__title">
        至今已有約17%被捕者，即1498人在經過調查後獲無條件釋放。
      </h2> */}
    </div>
  )
}

export default BarChartC