const timelineChart = () => {
  const margin = { top: 5, right: 5, bottom: 20, left: 20 },
      width = 600,
      height = 200,
      parseTime = d3.timeParse("%m/%Y"),
      timeValue = function(d) { return parseTime(d.month); },
      dataValue = function (d) { return +d.value; },
      color = "steelblue";

  function tweenDash() {
    var l = this.getTotalLength(),
        i = d3.interpolateString("0," + l, l + "," + l);
    return function (t) { return i(t); };
  }
  const transition = (path) => {
    path.transition()
      .duration(2000)
      .attrTween("stroke-dasharray", tweenDash);
  }
  

  const linechart = (selection) => {
    selection.each(function (data) {
      data = data.map(function (d, i) {
        return { time: timeValue(d), value: dataValue(d) };
      });
      var x = d3.scaleTime()
        .rangeRound([0, width - margin.left - margin.right])
        .domain(d3.extent(data, function(d) { return d.time; }));
      var y = d3.scaleLinear()
        .rangeRound([height - margin.top - margin.bottom, 0])
        .domain(d3.extent(data, function(d) { return d.value; }));
      
      var line = d3.line()
        .x(function(d) { return x(d.time); })
        .y(function(d) { return y(d.value); });

      var svg = d3.select(this).selectAll("svg").data([data]);
      var gEnter = svg.enter().append("svg").append("g");

      gEnter.append("rect")
        .attr("width", width - margin.left - margin.right + 10)
        .attr("height", height - margin.top - margin.bottom + 10)
        .attr("transform", "translate(-5, -5)")
        .attr("fill", "#111")

      gEnter.append("path")
        .datum(data)
        .attr("class", "data")
        .attr("fill", "none")
        .attr("stroke", "#C41F1F")
        .attr("stroke-linejoin", "round")
        .attr("stroke-linecap", "round")
        .attr("stroke-width", 1);
    
      

      gEnter.append("g").attr("class", "axis x");
      gEnter.append("g").attr("class", "axis y")
        .append("text")
        .attr("fill", "#000")
        .attr("transform", "rotate(-90)")
        .attr("y", 6)
        .attr("dy", "0.71em")
        .attr("text-anchor", "end");
      gEnter.append("path")
        .attr("class", "data");

      var svg = selection.select("svg");
      svg.attr('viewBox', '0 0 ' + width + ' ' + height);
      var g = svg.select("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

      g.select("g.axis.x")
        .attr("transform", "translate(0," + (height - margin.bottom - 5) + ")")
        .call(d3.axisBottom(x).ticks(40))
        .select(".domain")
        .remove();

      g.select("g.axis.y")
        .attr("class", "axis y")
        .call(d3.axisLeft(y));

      g.select("path.data")
        .datum(data)
        .attr("d", line)
        .call(transition);
    });
  }

  linechart.margin = function (_) {
    if (!arguments.length) return margin;
    margin = _;
    return linechart;
  };

  linechart.width = function (_) {
    if (!arguments.length) return width;
    width = _;
    return linechart;
  };

  linechart.height = function (_) {
    if (!arguments.length) return height;
    height = _;
    return linechart;
  };

  linechart.parseTime = function (_) {
    if (!arguments.length) return parseTime;
    parseTime = _;
    return linechart;
  };

  linechart.timeValue = function (_) {
    if (!arguments.length) return timeValue;
    timeValue = _;
    return linechart;
  };

  linechart.dataValue = function (_) {
    if (!arguments.length) return dataValue;
    dataValue = _;
    return linechart;
  };

  return linechart;
}

const LineChart = (props) => {
  const linechart = React.createRef();

  useEffect(() => {
    const width = 800;
    const height = 200;
    const margin = 40;

    var lines = timelineChart();

    function resize() {
        if (d3.select(linechart.current).empty()) {
            return;
        }
        lines.width(+d3.select(linechart.current).style("width").replace(/(px)/g, ""))
            .height(+d3.select(linechart.current).style("height").replace(/(px)/g, ""));
        d3.select(linechart.current).call(lines);
    }
      
    const line_svg = d3.select(linechart.current)
      .datum(data_line)
      .call(lines) ;
  })

  return (
    <div className="chart linechart section" id="chart">
      <h2 className="chart__title">{props.title}</h2>
      <div ref={linechart}></div>
    </div>
  )
}

export default LineChart;