import React, { useEffect } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import pro_08 from '../Images/prosecution_08.jpg';
import pro_09 from '../Images/prosecution_09.jpg';
import pro_10 from '../Images/prosecution_10.jpg';
import pro_11 from '../Images/prosecution_11.jpg';
import './HoriScroll.scss'

gsap.registerPlugin(ScrollTrigger);

const HoriScroll = (props) => {
  const horiScroll = React.createRef();
  const images = [ pro_08, pro_09, pro_10, pro_11]
  
  useEffect(() => {
    const sections = gsap.utils.toArray("#horiscrollA .panel");
    gsap.to(sections, {
      xPercent: -100 * (sections.length - 1),
      ease: "none",
      scrollTrigger: {
        trigger: horiScroll.current,
        pin: true,
        scrub: 1,
        snap: 1 / (sections.length - 1),
        start: "top top",
        end: () => "+=" + horiScroll.current.offsetWidth,
        invalidateOnRefresh: true
      }
    });
  });

  return (
    <div className="horiscroll__wrapper section" ref={horiScroll} id="horiscrollA">
      <div className="horiscroll container">
        {images.map((image) =>
          <div className="panel" style={{ backgroundImage: `url(` + image + `)`}}>
          </div>
        )}
      </div>
    </div>
  )
}

export default HoriScroll;