import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3';
import './BarChartD.scss';

const BarChartE = (props) => {
  const barchartE = useRef(null)
  const dataset = [
    {label:"Art and Humanities", "Not Satisfied":25, "Not Much Satisfied":40, "Satisfied": 15, "Very Satisfied":20},
    {label:"Sciences", "Not Satisfied":5, "Not Much Satisfied":30, "Satisfied": 50, "Very Satisfied":15},
    {label:"Health Sciences", "Not Satisfied":20, "Not Much Satisfied":30, "Satisfied": 40, "Very Satisfied":10},
    {label:"Social Sciences", "Not Satisfied":10, "Not Much Satisfied":40, "Satisfied": 45, "Very Satisfied":5},
    {label:"Architecture and Engineering", "Not Satisfied":5, "Not Much Satisfied":15, "Satisfied": 40, "Very Satisfied":40},
  ];

  useEffect(() => {
    
    var barData = [{
      "id": 'test1',
      "verynotagree": 80.5,
      "notagree": 14.8,
      "neutral": 4,
      "agree": 0.6,
      "veryagree": 0.2,
    }, {
      "id": 'test2',
      "verynotagree": 13.3,
      "notagree": 28.8,
      "neutral": 41.0,
      "agree": 15.5,
      "veryagree": 1.4,
    }];
      var drawChart = function(data) {
        var margin = {
          t: 30,
          r: 20,
          b: 20,
          l: 70
        },
          width = 1000,
          height = 200;
        var w = width ;
        var h = height - margin.t - margin.b;
        
        
        var y = d3.scaleLinear().domain([0, data.length]).range([h - margin.t - margin.b, 0]);
        var x = d3.scaleLinear().range([0, w - margin.r - margin.l]);
        var x_label = d3.scaleLinear().range([0, w + 50]);
      
        var color = ["#C41F1F", "#EC6868", "#FFFFFF", "#6DB76D", "#389C38"];
        var getRanges = function(d) {
          var vn, na, ne, ag, va;
          var total = d.verynotagree + d.notagree + d.neutral + d.agree + d.veryagree
          vn = (d.verynotagree / total);
          na = (d.notagree / total);
          ne = (d.neutral / total);
          ag = (d.agree / total);
          va = (d.veryagree / total);
          return [[0, vn.toFixed(4)], [vn.toFixed(4), na.toFixed(4)], [(vn + na).toFixed(4), ne.toFixed(4)], [(vn + na + ne).toFixed(4), ag.toFixed(4)], [(vn + na + ne + ag).toFixed(4), va.toFixed(4)]];
        };

        const levels = ["非常不信任","不信任","一般","信任","非常信任"];

        var xAxis = d3.axisBottom()
          .scale(x_label)
          .ticks(5)
          .tickFormat(function(d, i){return levels[i]})

        
        const labels = ["律政司","法院", ""];
        var formatDay = function(d) {
          return labels[d % labels.length];      
        }
        
        
        var yAxis = d3.axisLeft()
          .scale(y)
          .tickFormat(formatDay);
      
        var svg = d3.select(barchartE.current).append("svg")
          .attr("viewBox", "0 0 " + w + " " + h)
          .attr("transform", "translate(0," + (margin.t) + ")");
      
        svg.append("g")
          .attr("class", "x axis")
          .attr("transform", "translate(" + (margin.l - 30) + "," + (margin.t - 10) + ")")
          .call(xAxis);
      
        svg.append("g")
          .attr("class", "y axis")
          .attr("transform", "translate(" + (width) + "," + (margin.t) + ")")
          .call(yAxis);

      
        data.forEach(function(td, j) {
          svg.append("g")
            .attr("transform", "translate(" + margin.l + "," + (y(j) + margin.t - 25/2) + ")")
            .selectAll("rect")
            .data(getRanges(td))
            .enter()
            .append("rect")
            .attr("class", "bar")
            .style("fill", function(d, i) {
              return color[i];
            })
            .attr("width", function(d, i) {
              return x(d[1]);
            })
            .attr("height", 40)
            .attr("transform", function(d, i) {
              return "translate(" + (x(d[0]) - margin.l) + ",0)";
            });
        });
      };
      drawChart(barData);
    
  }, [])

  return (
    <div
      className="chart section"
      ref={barchartE}
    >
      <h2 className="chart__title">{props.title}</h2>
    </div>
  )
}

export default BarChartE