import React, { useEffect } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import pro_04 from '../Images/prosecution_04.jpg';
import pro_05 from '../Images/prosecution_05.jpg';
import pro_06 from '../Images/prosecution_06.jpg';
import pro_07 from '../Images/prosecution_07.jpg';
import './VertiScroll.scss'

gsap.registerPlugin(ScrollTrigger);

const VertiScroll = (props) => {
  const horiScroll = React.createRef();
  const images = [pro_04, pro_05, pro_06, pro_07];

  useEffect(() => {
    const sections = gsap.utils.toArray(".image");
    sections.forEach((section, i) => {
      gsap.fromTo(section, 
        {
          opacity: -i + 1,
        },
        {
        opacity: -i + 5,
        ease: "none",
        scrollTrigger: {
          trigger: horiScroll.current,
          pin: section,
          scrub: 1,
          snap: 1 / (section.length - 1),
          start: "top top",
          endTrigger: horiScroll.current,
          end: "bottom 50%+=100px",
          invalidateOnRefresh: true
        }
      });
    });    
  });

  return (
    <div className="vertiscroll container section" ref={horiScroll}>
      <div className="vertiscroll__image">
        {images.map((image) =>
          <div className="image">
            <img src={image}  />
          </div>
        )}
      </div>
      <div className="vertiscroll__text" id="vertiscroll__text">
      去年11月，海關截查郵包時，發現內有丫叉及鋼珠，收件人正是暑假回港的美國大學生家朗，警方發現他牽涉另一宗示威案件，遂派人假扮送貨員送郵包到其單位。母親代為簽收郵包，家朗當時不在場，同日在原定報到的警署內被捕，被控企圖管有攻擊性武器罪。
      <br/><br/>
      裁判官指令還押，家朗強調不知道郵包背景，也沒有信用卡郵購，經過幾次申請，仍然未能保釋。還押72日，最終，律政司撤控。
      <br/><br/>
      蔡梓蘊這樣比喻撤控，「等於我打鑊你，唉sorry，我走先，不好意思對不起我打錯人。」
      <br/><br/>
      程序上，撤控的案件看似有可追究空間，但蔡梓蘊認為，現實中很困難，金錢、時間、證據也是要考慮的因素，「一個人剛無罪了，他會否想重新經歷一次司法程序？未必。」
      <br/><br/>
      家朗早前眾籌，希望以民事方式追討，但網站被攻擊，戶口遭凍結，官司暫時未有進展。
      <br/><br/>
      面對耗費時間和金錢的官司，一些示威者選擇最終認罪。
      <br/><br/>
      2020年5月15日，首宗暴動案罪成，被告認罪，刑期扣減三分一，最終獲判監禁4年。
      <br/><br/>
      一時之間，網絡上紛紛問：「為何要認罪？」。蔡梓蘊分析，當下各被告考慮的，不只是限於原則上有否犯事、或案件在法律角度「有無得打」，而是「你會否想打一宗官司？」
      <br/><br/>
      阿超一度也打算認罪了。
      <br/><br/>
      「刑期壓下來，你無理由不要扣減三分一。將心比己，你自己做過，有證據，你不認？不認，你就不要上法庭。我覺得，針不拮（刺）到肉，他們是不知痛。他們不會知我們在面對什麼。」
      <br/><br/>
      不過，在現時已進入司法程序的1979宗個案，已至少12人缺席聆訊。
      </div>
    </div>
  )
}

export default VertiScroll;