import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3';
import './BarChartB.scss';

const BarChartB = (props) => {
  const barchartB = useRef(null)
  
  useEffect(() => {
    var data = [{
      "name": "暴動",
      "value": 653,
      "desc": "10年",
    },
    {
      "name": "藏有攻擊性武器",
      "value": 304,
      "desc": "3年",
    },
    {
      "name": "非法集結",
      "value": 294,
      "desc": "5年",
    },
    {
      "name": "企圖襲警 / 襲警",
      "value": 140,
      "desc": "2年",
    },
    {
      "name": "刑事損壞",
      "value": 74,
      "desc": "10年",
    },
    {
      "name": "縱火",
      "value": 67,
      "desc": "終身監禁",
    },
    {
      "name": "國安罪",
      "value": 1,
      "desc": "終身監禁",
    }];
    

data = data.sort(function (a, b) {
    return d3.ascending(a.value, b.value);
})

var margin = {
    top: 15,
    right: 0,
    bottom: 15,
    left: 0
};

var width = 960 - margin.left - margin.right,
    height = 350 - margin.top - margin.bottom;

var svg = d3.select(barchartB.current).append("svg")
    .attr('viewBox', '0 0 ' + (width + margin.left + margin.right) + ' ' + (height + margin.top + margin.bottom))
    .append("g")
    .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

var x = d3.scaleLinear()
    .range([0, width])
    .domain([0, d3.max(data, function (d) {
        return d.value;
    })]);
    

var y = d3.scaleBand()
    .rangeRound([height, 0])
    .domain(data.map(function (d) {
        return d.name;
    }));


var yAxis = d3.axisLeft()
    .scale(y)
    .tickSize(0)
    .tickFormat(function(d, i){return data[i].value + " 人"})



var bars = svg.selectAll(".bar")
    .data(data)
    .enter()
    .append("g")

bars.append("rect")
  .attr("class", "bar")
  .attr("y", function (d) {
      return y(d.name);
  })
  .attr("height", y.bandwidth() * 0.85)
  .attr("x", 0)
  .attr("width", function (d) {
      return x(d.value);
  });

bars.append("text")
  .attr("class", "label")
  .attr("y", function (d) {
      return y(d.name) + y.bandwidth() / 2;
  })
  .attr("x", 15)
  .text(function (d) {
      return d.name + " " + d.desc;
  })
  .style("fill", '#fff')
  .style("font-size", "16px");
    
  var gy = svg.append("g")
    .attr("class", "y axis")
    .attr("transform", "translate( " + (width - 10)  + ", -4 )")
    .call(yAxis)
    .style("font-size", "16px");
    
  }, [])

  return (
    <div
      className="chart section"
      ref={barchartB}
    >
      <h2 className="chart__title">{props.title}</h2>
    </div>
  )
}

export default BarChartB