import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3';
import './BarChartB.scss';

const BarChartF = (props) => {
  const barchartF = useRef(null)
  
  useEffect(() => {
    var data = [{
      "name": "逾310人",
      "value": 310,
      "desc": "2次",
    },
    {
      "name": "30人",
      "value": 30,
      "desc": "3次",
    },
    {
      "name": "6人",
      "value": 6,
      "desc": "4次",
    },
    {
      "name": "1人",
      "value": 1,
      "desc": "9次",
    }];
    

data = data.sort(function (a, b) {
    return d3.ascending(a.value, b.value);
})

var margin = {
    top: 15,
    right: 0,
    bottom: 15,
    left: 0
};

var width = 960 - margin.left - margin.right,
    height = 150 - margin.top - margin.bottom;

var svg = d3.select(barchartF.current).append("svg")
    .attr('viewBox', '0 0 ' + (width + margin.left + margin.right) + ' ' + (height + margin.top + margin.bottom))
    .append("g")
    .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

var x = d3.scaleLinear()
    .range([0, width])
    .domain([0, d3.max(data, function (d) {
        return d.value;
    })]);
    

var y = d3.scaleBand()
    .rangeRound([height, 0])
    .domain(data.map(function (d) {
        return d.name;
    }));


var yAxis = d3.axisLeft()
    .scale(y)
    .tickSize(0)
    .tickFormat(function(d, i){return data[i].desc})



var bars = svg.selectAll(".bar")
    .data(data)
    .enter()
    .append("g")

bars.append("rect")
  .attr("class", "bar")
  .attr("y", function (d) {
      return y(d.name);
  })
  .attr("height", y.bandwidth() * 0.85)
  .attr("x", 0)
  .attr("width", function (d) {
      return x(d.value);
  });

bars.append("text")
  .attr("class", "label")
  .attr("y", function (d) {
      return y(d.name) + y.bandwidth() / 2;
  })
  .attr("x", 15)
  .text(function (d) {
      return d.name;
  })
  .style("fill", '#fff');
    
  var gy = svg.append("g")
    .attr("class", "y axis")
    .attr("transform", "translate( " + (width - 10)  + ", 0 )")
    .call(yAxis)
    
  }, [])

  return (
    <div
      className="chart section"
      ref={barchartF}
    >
      <h2 className="chart__title">{props.title}</h2>
    </div>
  )
}

export default BarChartF