      <Text type="content" text={["阿超第一次被捕就被控告暴動罪。去年十一國慶前夕，有市民發起9月29日「全球反極權大遊行」，當日有148人被捕，97人被控暴動，阿超是其中的一人。", <br/>, <br/>, "在警署羈留室內，大家知道這麼多人被控暴動的一刻，突然興奮疾呼，因為覺得警察只是在濫捕亂告。不過阿超感覺憂慮，因為暴動罪門檻很低，「就算你什麼也沒有做，你解釋不到，在現場也能入到你（罪）。」"]} />
