      <Text type="center" text={["截至6月30日，有304人的案件已經結案，定罪率不到一半，12宗為罪名不成立，無罪釋放。餘下個案大多屬簽保守行為，更有40宗由控方撤銷控罪，撤控率達13.1%。", <br/>, <br/>, "義務律師蔡梓蘊認為，撤控意味著沒有足夠證據就提出起訴，警方和律政司應該在證據充足之時才把案件提上法庭，她認為也值得留意其中是否涉及濫捕。"]} />
