import React, { useEffect, useState } from 'react';
import './Voice.scss'
import WaveSurfer from 'wavesurfer.js';

const Voice = (props) => {
  let [waveSurfer, setWaveSurfer] = useState(null)
  const [play, setPlay] = useState(false);
  
  const togglePlay = () => {
    waveSurfer.playPause();
    setPlay(!play)
  }
  useEffect(() => {
    setWaveSurfer(WaveSurfer.create({
      container: '#' + props.canvasID,
      waveColor: '#fff',
      progressColor: '#C41F1F',
      barWidth: 1,
      cursorColor: "#C41F1F"
    }))
    
  }, [])

  useEffect(() => {
    if(waveSurfer) {
      waveSurfer.load(props.audioSrc)
      waveSurfer.on('finish', function () {
        setPlay(false)
      });
    }
  }, [waveSurfer])
  return (
    <div className="voice section">
      {/* <audio id={props.audioID} src={props.audioSrc} ref={audio}></audio> */}
      <button onClick={togglePlay}>
        { props.name }
        <div className={!play ? "icon play" : "icon pause" }></div>
      </button>
      {/* <canvas id={props.canvasID} height="200" width="1000"></canvas> */}
      <div id={props.canvasID} className="voice__waveform"></div>
    </div>
  )
}

export default Voice;