import React, { useEffect, useState } from 'react';
import './VoicePhoto.scss'
import WaveSurfer from 'wavesurfer.js';

const VoicePhoto = (props) => {
  let [waveSurfer, setWaveSurfer] = useState(null)
  const [play, setPlay] = useState(false);
  
  const togglePlay = () => {
    waveSurfer.playPause();
    setPlay(!play)
  }
  useEffect(() => {
    setWaveSurfer(WaveSurfer.create({
      container: '#' + props.canvasID,
      waveColor: '#fff',
      progressColor: '#C41F1F',
      barWidth: 1,
      cursorColor: "#C41F1F"
    }))
  }, [])

  useEffect(() => {
    if(waveSurfer) {
      waveSurfer.load(props.audioSrc)
      waveSurfer.on('finish', function () {
        setPlay(false)
      });
    }
  }, [waveSurfer])
  return (
    <div className="voicePhoto section">
      <div className="voicePhoto__imagewrapper">
      <div className="voicePhoto__image" style={{ backgroundImage: "url(" + props.imageSrc + ")"}} />
      </div>
      <div className="voicePhoto__wrapper">
        <button onClick={togglePlay}>
          { props.name }
          <div className={!play ? "icon play" : "icon pause" }></div>
        </button>
        <div id={props.canvasID} className="voicePhoto__waveform"></div>
      </div>
    </div>
  )
}

export default VoicePhoto;
