import React, { useEffect } from 'react';
import './Cover.scss'
import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import * as d3 from 'd3';

gsap.registerPlugin(ScrollTrigger);

const sampleData = [
  { label: '學生', value: 634 },
  { label: '地盤、裝修 運輸、清潔工人', value: 123 },
  { label: '飲食', value: 84 },
  { label: '文職', value: 75 },
  { label: '政界', value: 43 },
  { label: '技工', value: 39 },
  { label: '售貨員', value: 34 },
  { label: '金融、地產 保險、經商', value: 30 },
  { label: '設計師', value: 24 },
  { label: '測量、會計 藥劑、機師 公務員', value: 27 },
  { label: '工程師', value: 18 },
  { label: '社工、醫護', value: 18 },
  { label: '教育', value: 16 },
  { label: '廣告、公關', value: 15 },
  { label: '資訊科技', value: 15 },
  { label: '主婦、退休', value: 13 }
]

const CircleChart = (props) => {  
  const cover = React.createRef();
  const bg = React.createRef();
  const circleChart = React.createRef();
  const height = 1440;
  const width = 1400;
  const radius = 30;
  function wrap(text, width) {
    text.each(function() {
      let text = d3.select(this),
        words = text.text().split(/\s+/).reverse(),
        word,
        line = [],
        lineNumber = 0,
        lineHeight = 1.4, // ems
        x = text.attr("x"),
        y = text.attr("y"),
        dy = 1.1,
        tspan = text.text(null).append("tspan").attr("x", x).attr("y", y).attr("dy", dy + "em");
      while (word = words.pop()) {
        line.push(word);
        tspan.text(line.join(" "));
        if (tspan.node().getComputedTextLength() > width) {
          line.pop();
          tspan.text(line.join(" "));
          line = [word];
          tspan = text.append("tspan").attr("x", x).attr("y", y).attr("dy", ++lineNumber * lineHeight + dy + "em").text(word);
        }
      }
    });
  }
  useEffect(() => {
    const svg_a = d3.select(circleChart.current)
    .append("svg")
    .attr("viewBox", "0 0 " + width +  " " + height)
    .append("g")


    const circles_a = d3.entries(sampleData).map((d, i) => ({
      x: i == 0 ? 725 : 310 * ((i - 1) % 5) + 100,
      y: i == 0 ? 280: 280 * Math.floor((i - 1)/5) - d.value.value / 1.1 + 800,
      r: Math.sqrt(d.value.value) * 8,
      label: d.value.label
    }));

    svg_a.selectAll("circle")
      .data(circles_a)
      .join("circle")
      .attr("cx", d => d.x)
      .attr("cy", d => d.y)
      .attr("r", d => d.r)
      .attr("fill", '#C41F1F')
    
    svg_a.selectAll('.text-label')
      .data(circles_a)
      .enter().append('text')
      .attr('class', 'text-label')
      .attr('text-anchor', 'middle')
      .attr('x', d => d.x)
      .attr('y', d => (d.y + 30 + d.r))
      .style('fill', '#fff')
      .text(d => d.label)
      .call(wrap, 100)
      .style("font-size", '24.5px')
      
  });
  return (
    <div className="chart section" ref={circleChart}>
      <h2 className="chart__title">{props.title}</h2>
    </div>
  )
}

export default CircleChart;