import React, { useEffect } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import pro_27 from '../Images/prosecution_27.jpg';
import pro_28 from '../Images/prosecution_28.jpg';
import pro_29 from '../Images/prosecution_29.jpg';
import pro_30 from '../Images/prosecution_30.jpg';
import './HoriScroll.scss'

gsap.registerPlugin(ScrollTrigger);

const HoriScroll = (props) => {
  const horiScrollB = React.createRef();
  const images = [ pro_27, pro_28, pro_29, pro_30]
  
  useEffect(() => {
    const sections = gsap.utils.toArray("#horiscrollB .panel");
    gsap.to(sections, {
      xPercent: -100 * (sections.length - 1),
      ease: "none",
      scrollTrigger: {
        trigger: horiScrollB.current,
        pin: true,
        scrub: 1,
        snap: 1 / (sections.length - 1),
        start: "top top",
        end: () => "+=" + horiScrollB.current.offsetWidth,
        invalidateOnRefresh: true
      }
    });
  });

  return (
    <div className="horiscroll__wrapper section" ref={horiScrollB} id="horiscrollB">
      <div className="horiscroll container">
        {images.map((image) =>
          <div className="panel" style={{ backgroundImage: `url(` + image + `)`}}>
          </div>
        )}
      </div>
    </div>
  )
}

export default HoriScroll;