import pro_05 from '../Images/mobile_prosecution_05.jpg';
import pro_06 from '../Images/mobile_prosecution_06.jpg';
import pro_07 from '../Images/mobile_prosecution_07.jpg';
import pro_08 from '../Images/mobile_prosecution_08.jpg';
import pro_09 from '../Images/mobile_prosecution_09.jpg';
import pro_10 from '../Images/mobile_prosecution_27.jpg';
import pro_11 from '../Images/mobile_prosecution_11.jpg';
import pro_12 from '../Images/mobile_prosecution_33.jpg';
import pro_13 from '../Images/mobile_prosecution_32.jpg';
import pro_14 from '../Images/mobile_prosecution_29.jpg';
import pro_15 from '../Images/mobile_prosecution_30.jpg';
import pro_16 from '../Images/mobile_prosecution_32.jpg';
import pro_17 from '../Images/mobile_prosecution_31.jpg';
import pro_18 from '../Images/mobile_prosecution_24.jpg';
import pro_19 from '../Images/mobile_prosecution_10.jpg';
import pro_20 from '../Images/mobile_prosecution_25.jpg';
import pro_21 from '../Images/mobile_prosecution_28.jpg';
import pro_22 from '../Images/mobile_prosecution_33.jpg';
