import pro_24 from '../Images/mobile_prosecution_21.jpg';
import pro_25 from '../Images/mobile_prosecution_22.jpg';
import './PhotoGallery.scss'

gsap.registerPlugin(ScrollTrigger);

const VertiScroll = (props) => {
  const horiScroll = React.createRef();
  const articleData = [
    { position: "-50vw, -50vh", position2: "-65vw, -70vh", rotation: "5deg", image: pro_01},
    { position: "-40vw, -40vh", position2: "-65vw, -60vh", rotation: "-15deg", image: pro_02},
    { position: "-5vw, -55vh", position2: "0vw, -75vh", rotation: "30deg", image: pro_03},
    { position: "15vw, -55vh", position2: "25vw, -75vh", rotation: "10deg", image: pro_04},
    { position: "40vw, -42vh", position2: "55vw, -76vh", rotation: "40deg", image: pro_05},
    { position: "40vw, 0vh", position2: "80vw, -30vh", rotation: "-25deg", image: pro_06},
    { position: "40vw, 20vh", position2: "60vw, 30vh", rotation: "110deg", image: pro_07},
    { position: "-10vw, -10vh", position2: "-45vw, -5vh", rotation: "95deg", image: pro_25},
    { position: "-25vw, 30vh", position2: "-65vw, 55vh", rotation: "-130deg", image: pro_08},
    { position: "-25vw, 50vh", position2: "-75vw, 80vh", rotation: "20deg", image: pro_09},
    { position: "-45vw, 40vh", position2: "-85vw, 60vh", rotation: "-80deg", image: pro_10},
    { position: "-40vw, 10vh", position2: "-88vw, 0vh", rotation: "30deg", image: pro_11},
    { position: "40vw, -10vh", position2: "88vw, 0vh", rotation: "100deg", image: pro_24},
    { position: "30vw, 50vh", position2: "65vw, 70vh", rotation: "40deg", image: pro_12},
    { position: "30vw, 30vh", position2: "55vw, 40vh", rotation: "50deg", image: pro_13},
    { position: "-25vw, -40vh", position2: "-45vw, -70vh", rotation: "10deg", image: pro_14},
    { position: "5vw, 25vh", position2: "45vw, 90vh", rotation: "40deg", image: pro_15},
    { position: "-10vw, 50vh", position2: "-60vw, 105vh", rotation: "-20deg", image: pro_16},
    { position: "10vw, 10vh", position2: "40vw, 35vh", rotation: "60deg", image: pro_17},
    { position: "20vw, -30vh", position2: "40vw, -45vh", rotation: "50deg", image: pro_18},
    { position: "10vw, -10vh", position2: "60vw, -15vh", rotation: "80deg", image: pro_19},
    { position: "-15vw, 10vh", position2: "-45vw, 40vh", rotation: "-80deg", image: pro_20},
    { position: "-15vw, -25vh", position2: "-45vw, -50vh", rotation: "-10deg", image: pro_21},
    { position: "-50vw, -10vh", position2: "-80vw, -20vh", rotation: "100deg", image: pro_22},
    { position: "-25vw, -15vh", position2: "-60vw, -40vh", rotation: "-60deg", image: pro_23},
  ]
  
  useEffect(() => {
    const sections = gsap.utils.toArray(".thumbnail");
    sections.forEach((section, i) => {
      gsap.fromTo(section, 
      {
        transform: "translate(" + articleData[i].position + ") rotate(" + articleData[i].rotation + ")",
      },
      {
        transform: "translate(" + articleData[i].position2 + ") rotate(" + articleData[i].rotation + ")",
        ease: "none",
        scrollTrigger: {
          trigger: horiScroll.current,
          pin: true,
          scrub: 1,
          start: "20% top",
          end: "+=100%",
          invalidateOnRefresh: true
        }
      });
    });    
  });

  return (
    <div className="photogallery section" ref={horiScroll}>
      <div className="photogallery__title">「一個人犯法是犯法問題，<br/>200萬人犯法是政治問題」</div>
      <div className="photogallery__image">
        {articleData.map((article) =>
          <img src={article.image} className="thumbnail" />
        )}
      </div>
    </div>
  )
}

export default VertiScroll;