import React, { useEffect } from 'react';
import './Cover.scss'
import { gsap, TweenLite } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import * as d3 from 'd3';
import bgImage from '../Images/cover.jpg';

gsap.registerPlugin(ScrollTrigger);

const Cover = () => {  
  const cover = React.createRef();
  const bg = React.createRef();
  const circleChart = React.createRef();
  const circleChartA = React.createRef();
  const circleChartB = React.createRef();
  const redtext = React.createRef();
  const masktext = React.createRef();
  const height_a = 630;
  const height_b = 895;
  const width = 900;
  const radius = 35;
  useEffect(() => {
    const svg_a = d3.select(circleChartA.current)
    .append("svg")
    .attr("viewBox", "0 0 " + width +  " " + height_a)
    .append("g")

    const svg_b = d3.select(circleChartB.current)
    .append("svg")
    .attr("viewBox", "0 0 " + width +  " " + height_b)
    .append("g")

    const circles_a = d3.range(70).map(i => ({
      x: 91.5 * (i % 10) + 20 + radius/2,
      y: 91.5 * Math.floor(i/10) + radius,
    }));

    const circles_b = d3.range(92).map(i => ({
      x: 91.5 * (i % 10) + 20 + radius/2,
      y: 91.5 * Math.floor(i/10) + radius,
      color: i > 72 ? '#C41F1F' : '#fff'
    }));

    svg_a.selectAll("circle")
      .data(circles_a)
      .join("circle")
      .attr("cx", d => d.x)
      .attr("cy", d => d.y)
      .attr("r", radius)
      .attr("fill", '#6DB76D')
    
    svg_b.selectAll("circle")
      .data(circles_b)
      .join("circle")
      .attr("cx", d => d.x)
      .attr("cy", d => d.y)
      .attr("r", radius)
      .attr("fill", d => d.color)
    

    let circleHeight = circleChart.current.offsetHeight
    gsap.timeline({scrollTrigger:{
      trigger: cover.current,
      scrub: true,
      pin: true,
      start: "top top",
      end: "+=" + (circleHeight) + "px",
      invalidateOnRefresh: true
    }})
    

    gsap.timeline({scrollTrigger:{
      trigger: circleChart.current,
      scrub: true,
      pin: true,
      start: "top top",
      end: "+=100%"
    }})
    .from(bg.current, {opacity: 0, ease: "none"})
    .to(circleChart.current, {opacity: 1, ease: "none", delay: 0.2})

    gsap.timeline({scrollTrigger: redtext.current})
    .fromTo(redtext.current, {opacity: 1}, {opacity: 1, duration: 1, ease: "none"})
    .fromTo(redtext.current, {opacity: 1}, {opacity: 0, duration: 1, ease: "none"})

    gsap.timeline({scrollTrigger: redtext.current})
    .fromTo(masktext.current, {opacity: 0}, {opacity: 1, duration: 1, delay:1, ease: "none"})

    // gsap.fromTo(redtext.current, {
    //   opacity: 1
    //   },{
    //   scrollTrigger: redtext.current, // same as scrollTrigger: {trigger: ".line-1", toggleActions: "play none none none", start: "top bottom", end: "bottom top"}
    //   opacity: 1, 
    //   duration: 1, 
    //   ease: "none"
    //   })
    //   .fromTo(redtext.current, {
    //   opacity: 1
    //   },{
    //   scrollTrigger: redtext.current, // same as scrollTrigger: {trigger: ".line-1", toggleActions: "play none none none", start: "top bottom", end: "bottom top"}
    //   opacity: 0, 
    //   duration: 1, 
    //   ease: "none"
    // })

  });
  return (
    <>
      <div className="cover" ref={cover}>
        <img src={bgImage} className="cover__bg--mask" alt="bg" ref={masktext} />
        <img src={bgImage} className="cover__bg" alt="bg" ref={bg}/>
        <div className="cover__invert" ref={redtext}></div>
      </div>
      <div className="cover__chart" ref={circleChart}>
        <div className="cover__chart__a" ref={circleChartA}>
          <div className="cover__chartmark">
            <div className="cover__charttitle">全港監獄<br/>在囚人數</div>
            <div className="cover__chartnumber">7023</div>
          </div>
        </div>
        <div className="cover__chart__b" ref={circleChartB}>
          <div className="cover__chartmark bottom">
            <div className="cover__charttitle">遭檢控人數</div>
            <div className="cover__chartnumber">1972</div>
          </div>
          <div className="cover__chartmark middle">
            <div className="cover__charttitle">反修例<br/>運動中的<br/>被捕人數</div>
            <div className="cover__chartnumber">9216</div>
          </div>
        </div>
      </div>
      <div className="cover cover__text">
        <div className="cover__invert" ref={redtext}></div>
      </div>
    </>
  )
}

export default Cover;